<template>
  <div>
    <Ddtop mes="我的订单"></Ddtop>
    <div class="select-dd">
        <div>
          <div v-for="(dh,a) in daohang" :key="a" @click="ddshaixuan(a)" :class="{xuanzhong: color === a}">
            {{dh}}
          </div>
        </div>
    </div>

  <Mydd :daohang="color"></Mydd>
  </div>
</template>

<script>
  import Ddtop from "../../components/dingdan-top.vue"
import Mydd from "../../components/mydd-main.vue"

  export default {
    name: 'mydingdan',
    components: {
    Ddtop,
    Mydd
    },
    data(){
      return{
        color: 0,
        daohang: ["全部","待付款","待发货","配送中","待评价"]
      }
    },
    methods:{
      ddshaixuan(a){
        this.color = a
        // console.log(this.color)
      }
    },
      created() {
          console.log(this.$route.params.num)
          if(this.$route.params.num){
              this.color = this.$route.params.num
          }
      }
  }
</script>

