<template>
  <div>
    <div class="mydd-main" v-for="(d,a) in dd" :key="a">
      <!-- 订单时间 -->
      <div class="dd-top">
          <div>
            {{d.time}}
          </div>
          <div>
            {{d.tname}}
          </div>
      </div>
<!-- 订单商品详情 -->
      <div class="ddlist" v-for="(list,b) in d.shangpinlist" :key="b">
        <div class="listimg">
          <img :src="list.img" alt="">
        </div>
        <div class="listjj">
          <div class="listname">
            {{list.name}}
          </div>
          <div class="listzongjia">
            <p>￥{{list.jiage}}</p>
            <p>×{{list.shuliang}}</p>
          </div>
        </div>
      </div>
<!-- 商品总结 -->
      <div class="dd-zongjie">
          共1件商品  合计:¥{{d.heji}}(含运费¥10)
      </div>
<!-- 不同种类的不同按钮 -->
      <div class="dd-bottom">
        <div class="green" v-show="d.type === 1" @click="lijifk(d)">
          立即付款
        </div>
        <div v-show="d.type === 1">
          朋友代付
        </div>
            <div v-show="d.type === 1" @click="quxiaodd()">
              取消订单
            </div>
            <div v-show="d.type === 2">
              催促发货
            </div>
            <div v-show="d.type === 2">
              取消订单
            </div>

            <div v-show="d.type === 3">
              确认收货
            </div>
            <div v-show="d.type === 3">
              延长收货
            </div>
            <div v-show="d.tname === '待评价'">
              立即评价
            </div>
            <div v-show="d.tname === '交易完成'">
              追加评价
            </div>
            <div v-show="d.type === 4" @click="shanchudd()">
              删除订单
            </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'mydd-main',
    props:{
      daohang: Number
    },
    data(){
      return{
        dd:[]
      }
    },
    mounted() {
      // console.log(this.daohang)
      this.$axios.get("/json/mydingdan.json").then(res=>{
        for(var i=0;i<res.data.length;i++){
          if(this.daohang == res.data[i].type){
          this.dd.push(res.data[i])
          var success = true
        }
        }
        if(success){
          // console.log(this.dd)
        }else{
          this.dd = res.data
        }
      })
    },
    // 实时监视传值改变订单类型
    watch: {
      daohang(){
        // 清空之前的订单信息
        this.dd = []
        this.$axios.get("/json/mydingdan.json").then(res=>{
        for(var i=0;i<res.data.length;i++){
          if(this.daohang == res.data[i].type){
          this.dd.push(res.data[i])
          var success = true
        }
        }
        if(success){
          // console.log(this.dd)
        }else{
          this.dd = res.data
        }
      })
      }

    },
    methods:{
      quxiaodd(){
      if(confirm("是否取消该条订单？")){
          alert("取消订单成功！")

      }else{
        console.log("取消!")
      }
    },
    shanchudd(){
      if(confirm("是否删除该条订单信息？")){
          alert("删除成功！")

      }else{
        console.log("取消!")
      }
    },
    // 未付款的订单立即付款（这里暂时没有15分钟限制）
    lijifk(data){
      this.$store.state.heji = data.heji
      this.$store.state.dingdan = data.goxinxi
      this.$store.state.shangpinlist = data.shangpinlist
      this.$router.push("/querendd")
    }
  }
}
</script>

<style scoped>
  .mydd-main{
    height: 370px;
    padding-left: 30px;
    padding-right: 30px;
    width: 100%;
    border-bottom: 20px solid #eeeeee;
    font-size: 24px;
    color: #515151;
  }

  .mydd-main .dd-top{
    height: 55px;
    line-height: 55px;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  .mydd-main .dd-top>div:last-child{
    color: #84c225;
  }

.mydd-main .ddlist{
  height: 160px;
  width: 100%;
}
.mydd-main .ddlist .listimg{
  display: inline-block;
  width: 160px;
  height: 160px;
  overflow: hidden;
}
.mydd-main .ddlist .listjj{
  float: right;
  width: calc(100% - 160px);
  padding-left: 20px;
  display: flex;
  justify-content: space-between;
}
.mydd-main .ddlist .listjj .listname{
  font-size: 30px;
  color: #1b1b1b;
}

.mydd-main .ddlist .listjj .listzongjia>p:first-child{
  font-size: 30px;
  color: #1b1b1b;
  text-align: right;
}
.mydd-main .ddlist .listjj .listzongjia>p:last-child{
  font-size: 24px;
  color: #515151;
  text-align: right;
}

.mydd-main .dd-zongjie{
  height: 60px;
  line-height: 60px;
  text-align: right;
  border-bottom: 1px solid #dddddd;
  padding-right: 20px;
}


.mydd-main .dd-bottom{
  height: 70px;
  width: 100%;
}
.mydd-main .dd-bottom>div{
  width: 130px;
  height: 60px;
  border-radius: 30px;
  border: 1px solid #888888;
  line-height: 60px;
  text-align: center;
  margin-left: 10px;
  float: right;
  margin-top: 6px;
}

.mydd-main .dd-bottom .green{
  color: #c1e092;
  border: 1px solid #c1e092;
}
</style>
